import * as React from "react"
import Layout from '../components/layout'
import { StaticImage } from 'gatsby-plugin-image'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Card from 'react-bootstrap/Card'
import {
  row,
  col,
  right
} from './index.module.css'


const IndexPage = () => {

  return (
    <Layout pageTitle="">
      <Row className={row}>
        <Col xs={12} md={6} className={col}>
          <Card>
            <Card.Img variant="bottom" src="https://github.com/erwintobing15/erwintobing15.github.io/blob/source/assets/dota2web/thumbnail.PNG?raw=true" />
          </Card>
        </Col>
        <Col xs={12} md={6} className={col} className={right}>
          <Card>
            <Card.Img variant="bottom" src="https://github.com/erwintobing15/erwintobing15.github.io/blob/source/assets/cppanalyzer/thumbnail.png?raw=true" />
          </Card>
        </Col>
      </Row>
      <Row className={row}>
        <Col xs={12} md={6} className={col}>
          <Card>
            <Card.Img variant="bottom" src="https://github.com/erwintobing15/erwintobing15.github.io/blob/source/assets/daimatus/thumbnail.png?raw=true" />
          </Card>
        </Col>
        <Col xs={12} md={6} className={col} className={right}>
          <Card>
            <Card.Img variant="bottom" src="https://github.com/erwintobing15/erwintobing15.github.io/blob/source/assets/deteksi/thumbnail.PNG?raw=true" />
          </Card>
        </Col>
      </Row>
    </Layout>
  )
}

export default IndexPage
